@import url(https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu+Mono:ital,wght@1,700&display=swap);
.container {
    position: fixed;
    height: 250px;
    width: 90%;
    left: 0;
    margin: 0 5%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgba(77, 77, 77, 0.9);
    border-radius: 0 0 80px 80px;
    z-index: 999;
}

.head {
    position: relative;
    height: 80%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 100px;
    padding: 0 10px;

    border: 1px solid rgba(148, 148, 148, 0.4);
    border-radius: 10px;
}

.profile {
    display: flex;
    align-items: center;
    justify-content: center;
}

.underline{
    position: relative;
    margin-left: 50px;
    font-size: 65px;
    letter-spacing: 4px;
    font-family: 'Amatic SC', cursive, sans-serif;
}

.underline:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 0;
    bottom: 0;
    left: 0;
    background-color: rgb(148, 8, 148);
    visibility: hidden;
    transition: all 0.3s ease-in-out;
}

.underline:hover:before {
    visibility: visible;
    height: 4px;
}



@media (max-width: 760px) {
    .container {
        height: 20vh;
        width: 100vw;
        top: 0;
        margin: 0;
        padding-top: 10px;
        border-radius: 0;
        background-color: rgba(67, 67, 67, 1);
    }

    .head {
        height: 80%;
        width: 90%;
        flex-direction: column;
        justify-content: center;
    }

    .profile {
        margin-top: 86px;
        padding-bottom: 15px;
        justify-content: space-between;
    }

    .underline {
        margin-left: 30px;
        font-size: 30px;
        color: #fff;
    }
}

.avatar {
    width: 150px;
    max-height: 150px;
    border-radius: 50%;
    overflow: hidden;
    border: 1px solid black;
    background-color: #fff;
}

.avatar img {
    width: 100%;
    height: 100%;
    transform: scale(1.3);
}

@media (max-width: 760px) {
    .avatar {
        width: 70px;
        max-height: 70px;
    }
}

.social {
    position: absolute;
    height: 100%;
    width: 40px;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    background-color: rgb(37, 37, 37);
    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;
}

.social a:nth-child(1) {
    color: #3a60b3;
}

.social a:nth-child(1):hover {
    color: #8b9dc3;
}

.social a:nth-child(2) {
    color: #0077b5;
}

.social a:nth-child(2):hover {
    color: #00a0dc;
}

.social a:nth-child(3) {
    color: #833AB4;
}

.social a:nth-child(3):hover {
    color: #E1306C;
}

@media (max-width: 760px) {
    .social {
        position: relative;
        display: flex;
        flex-direction: row;
        padding: 16px;
        border-radius: 0;
        border-bottom-right-radius: 10px;
        border-bottom-left-radius: 10px;
        transform: scale(.8);
        width: 80vw;
        margin: 13px;
        margin-top: 8px;
        background-color: rgba(67, 67, 67, 1);
    }

    .social a {
        margin: 0 30px;
        transform: scale(1.2);
    }
}

.main {
    width: 80%;
    margin: 340px auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;

    background: rgba(0, 10, 0, 0.1);
    border-radius: 10px;
}

@media (max-width: 760px) {
    .main {
        width: 100vw;
        position: relative;
        margin: 0;
        padding-top: 240px;
    }
}

.card {
  height: 440px;
  padding: 20px;
  margin: 30px 20px;
  display: flex;
  justify-content: center;

  background: rgba(0, 10, 0, 0.3);
  border-radius: 5%;
  cursor: pointer;
  transition: 0.3s ease;
}

.cardClose {
  display: none;
}

.cardOpen {
  position: fixed;
  height: 65vh;
  width: 40vw;
  top: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: rgba(0, 10, 0, 0.95);
  border-radius: 20px;
  cursor: default;
  z-index: 999;
}

.textOpen {
  height: 80%;
  color: #fff;
  font-size: 20px;
  line-height: 1.4rem;
  margin: auto 20px;
  padding: 20px 40px;
  border: 1px solid gray;
  border-radius: 12px;
}

.close-card {
  position: absolute;
  width: 100%;
  top: 20px;
}

.icon {
  background-color: lightgray;
  border-radius: 10px;
  cursor: pointer;
  transition: 0.2s ease;
}

.icon:hover {
  background-color: #fff;
  /* opacity: .7; */
  transform: scale(1.15);
}

.close-card span {
  position: absolute;
  height: 40px;
  width: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  right: 30px;
  font-weight: bold;
  cursor: pointer;
}

.textCard {
  max-width: 360px;
  max-height: 400px;
  margin: 10px;
  line-height: 1.5;
  white-space: pre-wrap;
  font-size: 14px;
  color: #fff;
  cursor: pointer;
}

.card:hover {
  background: rgba(0, 10, 0, 0.6);
  transform: scale(1.1);
  transition: 0.3s ease;
}

@keyframes appear {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.animate-appear {
  animation-name: appear;
  animation-duration: 700ms;
  animation-fill-mode: backwards;
}

