@media screen and (max-width: 1240px) {
  .cardOpen {
    height: 65vh;
    width: 40vw;
    top: 30vh;
  }

  .textOpen {
    font-size: clamp(14px, 16px, 18px);
  }
}

@media screen and (max-width: 1080px) {
  .cardOpen {
    height: 80vh;
    width: 60vw;
    top: 10vh;
  }

  .textOpen {
    font-size: clamp(16px, 18px, 20px);
  }
}

@media (max-width: 760px) {
  .card:first-child {
    margin-top: 20px;
    height: 490px;
  }

  .card {
    background: linear-gradient(to top left, #505050, rgb(13, 13, 13)) no-repeat;
  }

  .card:hover {
    transform: scale(1.03);
    transition: 0.3s ease;
    background: linear-gradient(to top left, #505050, rgb(13, 13, 13)) no-repeat;
  }

  .textCard {
    font-size: 14px;
    font-family: 'Ubuntu Mono', monospace, sans-serif;
    color: #fff;
    line-height: 1.4;
  }

  .cardOpen {
    height: 90vh;
    width: 90vw;
    top: 5vh;
    left: auto;
    background: linear-gradient(to top left, #505050, rgb(13, 13, 13) 80%) no-repeat;
  }

  .textOpen {
    height: 84%;
    margin-top: 40px;
    font-size: 16px;
    line-height: 1.4;
    font-family: 'Ubuntu Mono', monospace, sans-serif;
    color: #fff;
  }
}

body {
  margin: 0;
  padding: 0;
  display: flex;
  /* flex-direction: column; */
  justify-content: center;
  align-items: center;
  font-family: sans-serif;
  box-sizing: border-box;
  background: linear-gradient(to top left, #e66465, purple) no-repeat;
}

@media (max-width: 760px) {
  body {
    background: #171717;
  }
}

